import {getStyle} from '../../config/mUtils'
export const loadMore={
    directives: {
        'loadMore':{
            bind:(el,binding) => {
                
                let widonWHeight=window.screen.height;//
                let height;//
                let setTop;//
                let paddingButton;
                let marginButton;
                let requestFrom;//
                let oldScrollTop;
                let scrollEl;
                let heightEl;
                let scrollType=el.attributes.type && el.attributes.type.value;//type
                let scrollReduce=2;
                //22body
                if(scrollType == 2)
                {
                    scrollEl=el;
                    heightEl=el.children[0];
                }else{
                    scrollEl=document.body;
                    heightEl=el;
                }
                //
                el.addEventListener('touchstart',() =>{
                    console.log("")
                    height=heightEl.clientHeight;//DOM
                    if(scrollType==2)
                    {
                        height=height;
                    }
                    setTop=el.offsetTop;//DOM
                    paddingButton=getStyle(el,'paddingBottom');
                    marginButton=getStyle(el,'marginBottom');
                },false);
                //
                el.addEventListener('touchmove',()=>{
                    loadMore();
                },false);
                //
                el.addEventListener('touchend',()=>{
                    oldScrollTop=scrollEl.scrollTop;//
                    moveEnd();
                },false);
                const moveEnd=()=>{
                    //window.requestAnimationFrame() 
					//
                    //
                    requestFrom=requestAnimationFrame(()=>{
                        if(scrollEl.scrollTop!=oldScrollTop)
                        {
                            oldScrollTop=scrollEl.scrollTop;
                            moveEnd();
                        }else{
                            cancelAnimationFrame(requestFrom);//
                            //DOM
                            height=heightEl.clientHeight; 
                            loadMore();              
                        }
                    })
                }
                const loadMore=()=>{
                    //
                    //console.log(marginButton);
                    //console.log(scrollEl.scrollTop+widonWHeight+"----"+(height+setTop));
                     //+>=DOM+div++-2
                    if(scrollEl.scrollTop+widonWHeight >= height+setTop+marginButton+paddingButton-scrollReduce)
                    {
                       
                        binding.value();
                    }
                }
            }
        }
    }
}
//
export const getImgPath={
    methods:{
        getImgPath(path)
        {
            let suffix;
            if(!path)
            {
                return 'http://test.fe.ptdev.cn/elm/elmlogo.jpeg';
            }else if(path.indexOf('jpeg') !==-1)
            {
                suffix='.jpeg';
            }else{
                suffix='.png';
            }
        
         let url='/'+path.substr(0,1)+'/'+path.substr(1,2)+'/'+path.substr(3)+suffix;
         return 'https://fuss10.elemecdn.com'+url;
        }
    }
}


